html,
body {
  color: #fff;
  background: #181818;
  font-weight: normal;
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 2rem;
  margin-top: 0;
}
input:not([type="checkbox"]),
select,
textarea {
  border: 1px solid #d1d1d1;
  border-radius: 4px;
  box-shadow: none;
  box-sizing: border-box;
  height: 38px;
  padding: 6px 10px;
}
input,
textarea,
select,
fieldset {
  margin-bottom: 1.5rem;
}
label,
legend {
  display: block;
  font-weight: 600;
  margin-bottom: 0.5rem;
}
form {
  margin-bottom: 0;
}
p {
  margin-top: 0;
}
a {
  color: #1eaedb;
  text-decoration: none;
}
a:hover {
  color: #0fa0ce;
}
h1 {
  font-size: 4rem;
  letter-spacing: -0.1rem;
  line-height: 1.2;
}
h2 {
  font-size: 3.6rem;
  letter-spacing: -0.1rem;
  line-height: 1.25;
}
h3 {
  font-size: 3rem;
  letter-spacing: -0.1rem;
  line-height: 1.3;
}
h4 {
