.progress-container {
  background-color: #f5f5f5;
  border-radius: 0;
  box-shadow: none;
  height: 6px;
  position: relative;
  z-index: 1;
}
.progress-container .progress-bar {
  background-color: #2196f3;
  box-shadow: none;
  color: #fff;
  float: left;
  font-size: 12px;
  height: 100%;
  line-height: 23px;
  text-align: center;
  width: 0;
}
.progress-container .progress-bar::before {
  background-color: #cae6fc;
  content: "";
  display: block;
  height: 100%;
  left: 0;
  position: absolute;
  right: 0;
  width: 100%;
  z-index: -1;
}
.progress-container .progress-bar[data-progress] {
  height: 23px;
}
.progress-container .progress-bar[data-progress]::before {
  height: 23px;
}
.progress-container .progress-bar::after {
  content: attr(data-progress);
  font-weight: bold;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  right: 0;
  width: 4em;
}
.table-container table {
  border-collapse: collapse;
  color: #fff;
  font-size: 13px;
  margin: 0;
  margin-bottom: 2.5rem;
  overflow: hidden;
  overflow-x: auto;
  padding: 0;
  position: relative;
  text-align: left;
  white-space: nowrap;
  width: auto;
}
.table-container table.table-center {
  display: block;
  text-align: center;
}
.table-container table.table-center table {
  margin: 0 auto;
}
.table-container table tr {
  background-color: #282828;
  border: 1px solid #303030;
  border-top: 1px solid #353535;
}
.table-container table tr:nth-child(2n) {
  background-color: #303030;
}
.table-container table th,
.table-container table td {
  border: 1px solid #303030;
  padding: 12px 20px;
  padding-bottom: 8px;
}
.table-container table th {
  border: 1px solid #353535;
  box-sizing: border-box;
  font-size: 12px;
  font-weight: 600;
  height: 48px;
  letter-spacing: 0;
  line-height: 24px;
  padding: 12px 20px;
  padding-bottom: 8px;
  position: relative;
  text-align: left;
  text-overflow: ellipsis;
  vertical-align: bottom;
}
.label {
  background-color: #303030;
  border-radius: 0.25em;
  color: #fff;
  display: inline-block;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 1px;
  margin-left: 6px;
  padding: 0.2em 0.6em 0.3em;
  vertical-align: middle;
}
.parallax-container {
  position: relative;
}
.parallax-container .parallax {
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(4px);
  height: 20em;
  width: 100%;
}
.parallax-container .parallax.small {
  height: 15em;
}
.parallax-container .parallax.medium {
  height: 26em;
}
.parallax-container .parallax.large {
  height: 33em;
}
.parallax-container .content {
  left: 50%;
  position: absolute;
  text-align: center;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}
@media (max-width: 750px) {
  .markdown-container {
    padding: 2em 3em;
  }
  .paper-container .paper {
    padding: 40px 28px;
  }
  .fullscreen.half .background {
    height: 100%;
  }
  .me {
    padding: 0;
  }
  .me .title {
    font-size: 10vmin;
    margin: 0;
  }
  .me .image-title {
    height: 7em;
    width: 7em;
  }
  .me.unset {
    flex-direction: column;
  }
  .buttons {
    align-items: center;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
  }
  .buttons .btn {
    align-items: center;
    display: flex;
    font-size: 18px;
    justify-content: center;
    line-height: 20px;
    padding: 20px 10px;
    text-align: center;
    white-space: pre-line;
    width: calc(45% - 30px);
  }
  .buttons .btn i {
    margin-right: 10px;
  }
  .buttons.one-line .btn {
    padding: 14px 8px;
    width: 75%;
  }
}
